html, body {
  background: var(--background-color);
  font-size: var(--text-size);
  height: 100%;
  @apply bg-gray-900;
}


body {
  /* align-items: center; */
  /* display: flex; */
  /* flex-grow: 2; */
  /* flex-basis: 0%; */
  /* color: #f8edeb; */
  /* padding: 0 5px; */
  font-size: 30px;
  /* color: tomato; */
  font: 20px "Helvetica Neue", Helvetica, Arial, sans-serif;
}

html {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
html::-webkit-scrollbar {
  display: none;
}

#app{
  width: 100vw;
  max-width: 100%;
}

.card {
  overflow: scroll;
  width: -webkit-fill-available;
  max-width: -webkit-fill-available;
  height: -webkit-fill-available;
  max-height: -webkit-fill-available;
}

.windowColumn{
  margin: 5px;
  display: flex;
  flex-direction: column;
  margin-top: 2.8em;
}

.windowTitle {
  align-self: flex-end;
  font-size: small;
  margin-bottom: -10px;
  margin-right: 5px;
  background: var(--background-color);
  padding: 1px;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.resizeThis {
  resize: both;
  max-height: 400px;
  margin-right: 10px;
  overflow:scroll;
}

.tab {
  user-select: "none";
  /* padding: 4px; */
  margin: 0 0 1px 0;
  font: 1em;
  background:var(--accent-color);
  border-radius: 2px;
}
.tab div {
  background: var(--accent-color)
}

.tab.isDragging {
  background: var(--main-color);
}

.wrapper{
  /* @apply border-solid border-l-4 rounded-lg my-2; */
  padding: 2px;
  width: 100%;
  margin-bottom: 5px;
  background:var(--background-color);
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  border-color: var(--main-color);
  border-radius: 0.15em;
}

.wrapper.isDragging {
  background: var(--main-color);
}

.appHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    justify-items: baseline;
    align-items: baseline;

    position: fixed;
    /* background: var(--background-color); */
    width: 100%;
}

.appWrapper {
  display: flex;
  flex-direction: column;
}